import * as helpers from '../helpers';

/*
* TODO: add documentation
* */
export default function initFixedHeader(selector, onlyFixed = false, offset = 0) {
    const element = document.querySelector(selector);

    if (element === null) {
        return;
    }

    setTimeout(() => {
        const elementHeight = element.offsetHeight;
        const fixedPos = elementHeight + offset;
        const emptyElement = document.createElement('div');
        const elementStyle = getComputedStyle(element);
        const siblings = element.parentNode.children;
        let lastScrollPos = 0;

        if (!helpers.hasElement(siblings, emptyElement) && elementStyle.position !== 'absolute' && elementStyle.position !== 'fixed') {
            element.parentNode.insertBefore(emptyElement, element);
            emptyElement.style.height = `${elementHeight}px`;
        }

        helpers.addClass(element, '-absolute');
        // console.log(element, element.offsetHeight, element.clientHeight);
        // console.dir(element, element.offsetHeight);

        window.addEventListener('resize', helpers.throttle(() => {
            if (helpers.hasElement(siblings, emptyElement)) {
                emptyElement.style.height = `${element.offsetHeight}px`;
            }
        }));

        window.addEventListener('scroll', helpers.throttle(() => {
            const scroll = window.pageYOffset;

            if (scroll >= fixedPos) {
                helpers.addClass(element, '-is-fixed');

                if (!onlyFixed) {
                    if (scroll > lastScrollPos) {
                        setTimeout(() => {
                            helpers.removeClass(element, '-scroll-up');
                            helpers.addClass(element, '-scroll-down');
                        }, 250);
                    } else {
                        if (scroll + window.innerHeight < document.body.clientHeight) {
                            setTimeout(() => {
                                helpers.removeClass(element, '-scroll-down');
                                helpers.addClass(element, '-scroll-up');
                            }, 250);
                        }
                    }
                } else {
                    setTimeout(() => {
                        helpers.addClass(element, '-only-fixed');
                    }, 250);
                }
            } else {
                helpers.removeClass(element, '-is-fixed');
                if (!onlyFixed) {
                    helpers.removeClass(element, '-scroll-up');
                } else {
                    setTimeout(() => {
                        helpers.removeClass(element, '-only-fixed');
                    }, 250);
                }
            }

            lastScrollPos = scroll;
        }));
    }, 250);
}



