import '@babel/polyfill';
import objectFitImages from 'object-fit-images';
import './components/picturefill';
import * as helpers from './helpers';
import autocomplete from './components/autocomplete';
import initPreloader from './components/preloader';
import Dropdown from './components/dropdown';
import Megamenu from './components/megamenu';
import ResponsiveEvent from './components/responsive-event';
import initFixedHeader from './components/fixed-header';
import cookiesManagement from './components/cookies';
import linkExternalNewWindow from './components/new_window';
import accessTooltip from './components/accesstooltip';
import { enableDatePicker, enableTimePicker } from './components/stratis_datepicker';
import initGalleries from './components/galleries';
import toTop from '../components/go-to-top/go-to-top';
import flashInfoLocalstorage from '../components/flash-info/flash-info';
import managePageImage from '../components/page-image/page-image';
import initStratisMenu from '../components/stratis-menu/stratis-menu';
import drawChart from './components/chart';
import SlideToggler from './components/slideToggler';
import TabsCordion from './components/tabsCordion';
import initMulticategoies from './components/categories';
import initCarousels from './components/carousel';
import initMultiselect from '../components/multiselect/multiselect';
import formsHandler from './components/formsHandler';
import buttonsStateToggle from './components/buttons-state-toggle';
import projectVideo from '../components/projects/projects';
import Geocode from './components/geocode';
import TarteaucitronCookies from './components/tarteaucitron-stratis';
import SearchContent from './components/search-content';
import publicationSlider from '../components/publications/publication';
import interactiveMap from './components/interactive-map';
// import Readspeaker from '../components/read-speaker/read-speaker';

document.addEventListener('DOMContentLoaded', () => {
    window.addEventListener('responsive', () => {
        console.log('current breakpoint: ', window.responsive);
    });

    const event = new ResponsiveEvent();

    initCarousels();
    initStratisMenu();
    objectFitImages('img');
    toTop();
    cookiesManagement();
    linkExternalNewWindow();
    flashInfoLocalstorage();
    autocomplete();
    enableDatePicker();
    enableTimePicker();
    managePageImage();
    // initFixedHeader('.header:not(.-map) .header-bar');
    publicationSlider();
    initGalleries();
    drawChart('.chart');
    interactiveMap();

    const tooltipSettings = {
        objs: '.js-tooltip',
        tooltipClassName: 'access-tooltip',
        toolTipBetween: 15,
        toolTipUp: false,
        mouse: true,
        tempDelay: 6000,
        useAriaDP: false,
        useEscClose: true,
    };

    accessTooltip(tooltipSettings);
    initMulticategoies();
    initMultiselect();
    formsHandler();
    buttonsStateToggle();
    projectVideo();

    const geo = new Geocode();
    const electedName = new SearchContent('.contact-elected', {
        search: ['#keyword1', '#select1'],
        searchButton: '.filters__buttons .filters__button.-submit',
        item: '.contact__item',
    });

    // Comply to the european cookie law
    const cookies = new TarteaucitronCookies({
        language: 'fr',
        customText: {
            alertBigClick: '',
            alertBig: l10n.cookieMessage,
            alertBigPrivacy: 'Afin de vous proposer des vidos, des boutons de partage, des contenus remonts des rseaux sociaux et d\'laborer des statistiques de frquentation, nous sommes susceptibles de dposer des cookies tiers sur votre machine. Cela ne peut se faire qu\'en obtenant, au pralable, votre consentement pour chacun de ce cookies',
        },
        config: {
            cookieName: 'tartaucitron',
            hashtag: '#tarteaucitron', /* Ouverture automatique du panel avec le hashtag */
            highPrivacy: true, /* dsactiver le consentement implicite (en naviguant) ? */
            orientation: 'bottom', /* le bandeau doit tre en haut (top) ou en bas (bottom) ? */
            adblocker: false, /* Afficher un message si un adblocker est dtect */
            showAlertSmall: false, /* afficher le petit bandeau en bas  droite ? */
            cookieslist: false, /* Afficher la liste des cookies installs ? */
            removeCredit: true, /* supprimer le lien vers la source ? */
            handleBrowserDNTRequest: false,
            AcceptAllCta: true,
            moreInfoLink: true,
            // cookieDomain: '.example.com' /* Nom de domaine sur lequel sera pos le cookie pour les sous-domaines */
        },
    });

    // Add services to cookies manager
    cookies.addServices('youtube, vimeo, dailymotion');

    //Initializing click roll in the sidebar of map
    window.addEventListener('openPopup', () => {
        const clickRoll = new Dropdown('.click-roll', {
            wrapperClass: 'click-roll',
            dropdownClass: 'click-roll__block',
            btnClass: 'click-roll__toggle',
            closeOnOutsideClick: false,
            openTypeSlide: true,
            isWrapContent: true,
        });
    });

    document.createElement('picture');

    const drop = new Dropdown();

    // Create DefaultMenu with settings
    const menu = new Megamenu('.mnv-menu', {
        menu: {
            type: 'aside',
        },
    });

    const filter = new Dropdown('.filters__container', {
        wrapperClass: 'filters__container',
        dropdownClass: 'filters__dropdown',
        outsideToggle: true,
        outsideToggleId: 'filter-dropdown-toggle',
        closeOnOutsideClick: false,
        opened: ['desktop', 'tablet'],
    });

    const menuFloating = new Dropdown('.menu-floating', {
        wrapperClass: 'menu-floating',
        dropdownClass: 'menu-floating__list',
        closeOnOutsideClick: false,
        btnClass: 'menu-floating__btn',
        opened: ['desktop'],
    });

    const headerSearchForm = document.querySelector('.header-search__form');

    if (headerSearchForm !== null) {
        const searchDropdown = new Dropdown('.header-search', {
            wrapperClass: 'header-search',
            dropdownClass: 'header-search__form',
            btnClass: 'header-search__toggle',
            closeOnOutsideClick: true,
        });
    }

    const sameCategory = new Dropdown('.same-category__wrapper', {
        wrapperClass: 'same-category__wrapper',
        dropdownClass: 'same-category__list',
        btnClass: 'same-category__toggle',
        opened: ['desktop'],
    });

    const checkboxDropdown = new Dropdown('.checkbox-dropdown', {
        wrapperClass: 'checkbox-dropdown',
        dropdownClass: 'checkbox-dropdown__block',
        btnClass: 'checkbox-dropdown__toggle',
        closeOnOutsideClick: false,
    });

    const oneClick = new Dropdown('.one-click-panel', {
        wrapperClass: 'one-click-panel',
        dropdownClass: 'one-click-panel__section',
        closeBtn: true,
        closeBtnClass: 'one-click-panel__close',
        outsideToggle: true,
        outsideToggleId: 'js-one-click-toggle',
        focusTrap: ['desktop'],
        beforeOpen: () => {
            if (window.responsive === 'desktop') {
                helpers.addClass(document.body, '-overflow-hidden');
            }
        },
        afterClose: () => {
            if (window.responsive === 'desktop') {
                helpers.removeClass(document.body, '-overflow-hidden');
            }
        },
    });

    // Turn on slideToggle
    const toggleItems = [].slice.call(document.querySelectorAll('[data-sd-content]'));
    toggleItems.forEach((toggleItem) => {
        const toggleItemName = toggleItem.dataset.sdContent;

        const toggleTriggers = document.querySelectorAll(`[data-sd-trigger="${toggleItemName}"]`);
        const item = toggleTriggers.length > 0 && new SlideToggler(toggleItem);

        if (item) {
            toggleTriggers.forEach((toggleTrigger) => {
                const action = toggleTrigger.getAttribute('data-sd-action') || 'toggle';
                toggleTrigger.addEventListener('click', () => helpers.executeIfFunction(item[action].bind(item)));
            });
        }
    });

    // Add class to ancestor
    // Find ancestor and add to it a class
    // Just add to element data attribute with selector and class: data-add-ancestor="container-wrapper,-dark"
    const addClassToAncestors = [].slice.call(document.querySelectorAll('[data-add-ancestor]'));
    addClassToAncestors.forEach((child) => {
        const [selector, cls] = child.getAttribute('data-add-ancestor')
            .split(',', 2);
        const ancestor = helpers.findAncestor(child, selector);
        if (ancestor) {
            helpers.addClass(ancestor, cls);
        }
    });

    // Add this block with class <span class="ghost" data-remove-section="class"></span>
    const removeEmptySection = [].slice.call(document.querySelectorAll('[data-remove-section]'));
    removeEmptySection.forEach((child) => {
        const el = child.getAttribute('data-remove-section');
        const ancestor = helpers.findAncestor(child, el);
        if (ancestor) {
            ancestor.parentNode.removeChild(ancestor);
        }
    });

    const tabs = new TabsCordion('.tabs', {
        onlyState: 'tabs',
        titleSelector: '.tabs__title',
        afterInit: () => {
            accessTooltip(Object.assign(tooltipSettings, { objs: '.tabs .js-tooltip' }));
        },
    });

    // Readpseaker - See doc in read-speaker.js
    /*
    const initRS = new Readspeaker('article.container-wrapper .heading', {
        insertPosition: 'beforebegin',
        customerId: '10375',
        readId: null,
        readClass: 'heading__content,site-content',
        config: {
            general: {
                usePost: true,
            },
            ui: {
                disableDetachment: true,
                displayDownload: true,
                tools: {
                    translation: true,
                },
            },
        },
        preserveScript: '.click-roll, .localiser',
        ignoreContent: '.gallery, .media-element__wrapper, .localiser, .pager',
    });
    */
});

window.addEventListener('load', () => {
    initPreloader();

    const siteImages = [].slice.call(document.getElementsByTagName('img')); // Edge fix

    siteImages
        .filter(img => !img.hasAttribute('data-ofi-src')) // Exclude object-fit images
        .filter(img => img.src.indexOf('.svg') === -1) // Exclude svg since they can return 0 sometimes
        .forEach((img) => {
            if (!img.naturalHeight) {
                helpers.addClass(img, '-broken-img');
                img.setAttribute('height', 0);
            }
        });

    const clickRoll = new Dropdown('.click-roll', {
        wrapperClass: 'click-roll',
        dropdownClass: 'click-roll__block',
        btnClass: 'click-roll__toggle',
        closeOnOutsideClick: false,
        openTypeSlide: true,
        isWrapContent: true,
    });
});



