import $ from 'jquery';
import datePickerController from './lib_datepicker';
import './jquery.timepicker';

export function enableDatePicker() {
    const inputDatepicker = $('input[type=text].datepicker');
    const inputDate = $('input[type=date]:not(.datepicker)');

    // Input date is supported ?
    const hasNativeDatePicker = () => {
        const elem = document.createElement('input');

        elem.setAttribute('type', 'date');
        elem.value = 'foo';

        return (elem.type === 'date' && elem.value !== 'foo');
    };

    function frenchToEnglishDate(frenchDate, separatorIn, separatorOut) {
        console.log('frenchDate', frenchDate);
        const dateElem = frenchDate.split(separatorIn);
        return dateElem[2] + separatorOut + dateElem[1] + separatorOut + dateElem[0];
    }

    function showInternationalDate(argObj) {
        console.log('argObj', argObj);
        const argObjId = argObj.id || argObj.attr('id');
        const argObjDate = argObj.date || new Date(frenchToEnglishDate(argObj.val()), '-', '/');
        const inputHidden = document.getElementById(`${argObjId}-hidden`);

        inputHidden.value = datePickerController.printFormattedDate(argObjDate, '%Y-%m-%d', false);
    }

    function pluginDatepicker($this) {
        const thisId = $this.attr('id');
        const thisName = $this.attr('name');
        const obj = {};

        $this
            .attr('data-name', thisName)
            .removeAttr('name')
            .after(`<input type="hidden" name="${thisName}" id="${thisId}-hidden" value="">`)
            .on('change', () => {
                const inputHidden = $this.nextAll(`#${thisId}-hidden`);
                const formatDate = frenchToEnglishDate($this.val(), '/', '-');
                inputHidden.val(formatDate);
            });

        if ($this.val() !== '') {
            showInternationalDate($this);
        }

        // obj[thisId] = '%d/%m/%Y';
        obj[thisId] = '%Y-%m-%d';
        datePickerController.createDatePicker({
            formElements: obj,
            callbackFunctions: {
                datereturned: [showInternationalDate],
            },
        });
    }

    function nativeDatepicker($this) {
        $this.each(() => {
            const thisId = $this.attr('id');
            const thisValue = $this.val() || '';
            const thisName = $this.attr('name');

            $this
                .attr('data-name', thisName)
                .removeAttr('name')
                .after(`<input type="hidden" name="${thisName}" id="${thisId}-hidden" value="${thisValue}">`)
                .on('change', () => {
                    const inputHidden = $this.next();
                    inputHidden.val($this.val());
                });
        });
    }

    if (!hasNativeDatePicker()) { // If has no native datepicker (IE11)
        inputDatepicker.each((index, item) => {
            pluginDatepicker($(item));
        });
        inputDate.each((index, item) => {
            pluginDatepicker($(item));
        });
    } else { // If has native datepicker (Chrome, Firefox, Opera, Safari)
        inputDatepicker.each((index, item) => {
            pluginDatepicker($(item));
        });
        inputDate.each((index, item) => {
            nativeDatepicker($(item));
        });
    }
}


export function enableTimePicker() {
    $('.time input').timepicker({
        timeFormat: 'H:i',
    });
}



