import * as helpers from '../../js/helpers';

export default function toTop() {
    const btnToTop = document.querySelector('.go-to-top');

    if (btnToTop === null) {
        return;
    }

    const btnToTopLink = btnToTop.querySelector('a');
    const newsletter = document.querySelector('.newsletter');

    // sets the button position
    function setBtnPosition(scroll, btnHeight) {
        const newsletterHeight = helpers.outerHeight(newsletter);

        if (scroll) {
            btnToTop.style.position = 'fixed';
            btnToTop.style.bottom = `${btnHeight}px`;
        } else {
            btnToTop.style.position = 'absolute';
            btnToTop.style.bottom = `${newsletterHeight + btnHeight / 2}px`;
        }
    }

    // smooth scroll to top
    function runScrollAnimation(scrollDuration) {
        const scrollStep = -window.pageYOffset / (scrollDuration / 10);
        const scrollInterval = setInterval(() => {
            if (window.pageYOffset !== 0) {
                window.scrollBy(0, scrollStep);
            } else clearInterval(scrollInterval);
        }, 15);
    }

    // hides the button if the scroll is at the top
    function setBtnDisplay(style) {
        if (style) {
            helpers.removeClass(btnToTop, '-is-hide');
        } else {
            helpers.addClass(btnToTop, '-is-hide');
        }
    }

    function setGoToTop() {
        const documentHeight = document.body.clientHeight;
        const scrollPosition = window.pageYOffset + window.innerHeight;
        const newsletterHeight = helpers.outerHeight(newsletter);
        const btnHeight = helpers.outerHeight(btnToTopLink);

        if (window.pageYOffset) {
            setBtnDisplay(true);
        } else {
            setBtnDisplay(false);
        }

        if (scrollPosition < documentHeight - (newsletterHeight - (btnHeight / 2))) {
            setBtnPosition(true, btnHeight);
        } else {
            setBtnPosition(false, btnHeight);
        }
    }

    if (btnToTop) {
        window.addEventListener('scroll', helpers.throttle(() => {
            setGoToTop();
        }, 150));

        window.addEventListener('resize', helpers.throttle(() => {
            setGoToTop();
        }, 150));

        btnToTop.addEventListener('click', (event) => {
            event.preventDefault();
            runScrollAnimation(500);
        });
    }
}



