import * as helpers from '../../js/helpers';

export default function flashInfoLocalstorage() {
    const flashInfo = document.querySelector('.flash-info');

    if (flashInfo === null) {
        return;
    }

    const flashInfoItems = flashInfo.querySelectorAll('.flash-info__item');
    let convertedString = '';
    for (let i = 0; i < flashInfoItems.length; i++) {
        const content = flashInfoItems[i].querySelector('.flash-info__content');
        if (content !== null) {
            convertedString += content.textContent.replace(/\n/g, ' ').replace(/\s/g, '');
        }
    }

    if (convertedString.length < 1) {
        return;
    }

    const btnClose = document.querySelector('.flash-info__button.-close');
    const hash = helpers.generateHash(convertedString);
    const flashInfoModal = document.querySelector('.flash-info__modal');

    if (localStorage.getItem('flash') === null || Number(localStorage.getItem('flash')) !== hash) {
        flashInfo.style.display = 'block';
    }

    if (flashInfoModal) {
        const radioYes = flashInfoModal.getElementsByTagName('input')[0];
        const flashInfoModalSubmit = flashInfoModal.querySelector('.flash-info__modal-btn');

        flashInfoModalSubmit.addEventListener('click', () => {
            const flashInfoModalClose = document.querySelector('.fancybox-close-small');

            flashInfoModalClose.click();
            flashInfo.style.display = 'none';

            if (!radioYes.checked) {
                localStorage.setItem('flash', hash);
            }
        });
    } else {
        btnClose.addEventListener('click', () => {
            flashInfo.style.display = 'none';
            localStorage.setItem('flash', hash);
        });
    }
}




