import * as helpers from '../helpers';

const setDate = (datepicker) => {
    if (datepicker.value.length > 0) {
        const dateArray = datepicker.value.split('-');

        const day = document.getElementById('date-day');
        const month = document.getElementById('date-month');
        const year = document.getElementById('date-year');

        day.value = dateArray[2];
        month.value = dateArray[1];
        year.value = dateArray[0];
    }
};

const setSelectYear = (dateSelect) => {
    const select = dateSelect.parentNode.querySelector('select');
    if (!select) {
        return;
    }

    const selectedOption = select.querySelector(`option[value="${select.value}"]`);
    if (!selectedOption) {
        return;
    }

    const group = selectedOption.parentNode;
    if (group && group.nodeName === 'OPTGROUP' && group.hasAttribute('label')) {
        dateSelect.setAttribute('value', group.getAttribute('label'));
    }
};

const removeNameAttribute = (name, form) => {
    const field = form.querySelector(`[name="${name}"]`);

    if (field !== null) {
        if (field.value === '0' || field.value === '') {
            field.removeAttribute('name');
        }
    }
};

const checkControllerNameAttribute = (name, form) => {
    let relatedFields = [];

    if (name === 'tx_news_pi1[search][subject]') {
        relatedFields = relatedFields.concat([
            'tx_news_pi1[__referrer][@request]',
            'tx_news_pi1[__referrer][@extension]',
            'tx_news_pi1[__referrer][@vendor]',
            'tx_news_pi1[__referrer][@controller]',
            'tx_news_pi1[__referrer][@action]',
            'tx_news_pi1[__referrer][arguments]',
            'tx_news_pi1[__trustedProperties]',
        ]);
    }

    if (name === 'tx_news_pi1[overwriteDemand][datecomplete]') {
        relatedFields = relatedFields.concat([
            'tx_news_pi1[overwriteDemand][startFromThisDate]',
        ]);
    }

    if (name === 'tx_stratismap_location[keyword]') {
        relatedFields = relatedFields.concat([
            'tx_stratismap_location[__referrer][@extension]',
            'tx_stratismap_location[__referrer][@vendor]',
            'tx_stratismap_location[__referrer][@controller]',
            'tx_stratismap_location[__referrer][@action]',
            'tx_stratismap_location[__referrer][arguments]',
            'tx_stratismap_location[__referrer][@request]',
            'tx_stratismap_location[__trustedProperties]',
        ]);
    }

    removeNameAttribute(name, form);

    relatedFields.forEach((relatedField) => {
        removeNameAttribute(relatedField, form);
    });
};

export default function formsHandler() {
    const forms = [].slice.call(document.querySelectorAll('form'));

    /**
     * TODO : Replace jquery code on vanillajs code
     */

    $('button[type="reset"]').click(function () {
        const multiselect = $(this).parents('form').find('.multiselect');
        $(this).parents('form').find(':radio, :checkbox').prop('indeterminate', false);
        $(this).parents('form').find(':radio, :checkbox').prop('checked', false);
        $(this).parents('form').find('textarea:enabled, input:enabled:not([readonly]), select:enabled').val('');

        if (multiselect.length > 0) {
            const placeholder = multiselect.attr('data-placeholder');
            multiselect.find('.multiselect__toggle').text(placeholder);
        }

        return false;
    });

    if (forms.length) {
        forms.forEach((form) => {
            form.addEventListener('submit', (e) => {

                const datepicker = document.getElementById('input-date-hidden');

                if (datepicker !== null) {
                    setDate(datepicker);
                }

                const dateSelect = document.getElementById('tx_news_year');
                if (dateSelect !== null) {
                    setSelectYear(dateSelect);
                }

                const fieldsNameToRemove = [
                    'tx_news_pi1[overwriteDemand][period]',
                    'tx_news_pi1[overwriteDemand][related]',
                    'tx_news_pi1[overwriteDemand][categories]',
                    'tx_news_pi1[overwriteDemand][day]',
                    'tx_news_pi1[overwriteDemand][month]',
                    'tx_news_pi1[overwriteDemand][year]',
                    'tx_news_pi1[overwriteDemand][keywords]',
                    'tx_news_pi1[overwriteDemand][datecomplete]',
                    'tx_news_pi1[search][subject]',
                    'tx_news_pi1[overwriteDemand][tags]',
                    'tx_news_pi1[overwriteDemand][relatedLinks]',
                    'tx_news_pi1[overwriteDemand][related]',
                    'tx_news_pi1[overwriteDemand][relatedLocations]',
                    'tx_news_pi1[overwriteDemand][relatedAreas]',
                    'tx_simplepoll_polllisting[category]',
                    'tx_simplepoll_polllisting[status]',
                    'tx_stratismap_location[search][keywords]',
                    'tx_stratismap_location[search][categories][]',
                ];

                fieldsNameToRemove.forEach((field) => {
                    checkControllerNameAttribute(field, form);
                });
            });
        });
    }
}



