import $ from 'jquery';

function checkSocialCookieEnabled() {
    let socialCookieEnabled = false;
    if (typeof localStorage.getItem('cookiesFunctional') !== 'undefined'
        && localStorage.getItem('cookiesFunctional') !== null
        && localStorage.getItem('cookiesFunctional') === 'true') {
        socialCookieEnabled = true;
    }
    return socialCookieEnabled;
}

// Example of use
/* if(checkSocialCookieEnabled()) {
 alert('Social cookie is enabled');
 } */

function getCookie(checkName) {
    // first we'll split this cookie up into name/value pairs
    // note: document.cookie only returns name=value, not the other components
    const allCookies = document.cookie.split(';');
    let tempCookies = '';
    let cookieName = '';
    let cookieValue = '';
    let cookieFound = false;
    for (i = 0; i < allCookies.length; i++) {
        // now we'll split apart each name=value pair
        tempCookies = allCookies[i].split('=');
        // and trim left/right whitespace while we're at it
        cookieName = tempCookies[0].replace(/^\s+|\s+$/g, '');
        // if the extracted name matches passed checkName
        if (cookieName === checkName) {
            cookieFound = true;
            // we need to handle case where cookie has no value but exists (no = sign, that is):
            if (tempCookies.length > 1) {
                cookieValue = unescape(tempCookies[1].replace(/^\s+|\s+$/g, ''));
            }
            // note that in cases where cookie is initialized but no value, null is returned
            return cookieValue;
            break;
        }
        tempCookies = null;
        cookieName = '';
    }
    if (!cookieFound) {
        return null;
    }
}

function clearCookie(name, domain, path) {
    try {
        if (getCookie(name)) {
            const domainNew = domain || document.domain;
            const pathNew = path || '/';
            document.cookie = `${name}=; expires=${new Date()}; domain=${domainNew}; path=${pathNew}`;
        }
    } catch (err) {
        // console.log('Error with cookies');
    }
}
function removeFunctionalCookies() {
    const domainToUseForGoogle = location.hostname.replace('www', '');
    // dev
    clearCookie('__utma', domainToUseForGoogle, '/');
    clearCookie('__utmb', domainToUseForGoogle, '/');
    clearCookie('__utmc', domainToUseForGoogle, '/');
    clearCookie('__utmz', domainToUseForGoogle, '/');
    clearCookie('__utmv', domainToUseForGoogle, '/');
    clearCookie('comments_title', location.hostname, '/');
    clearCookie('comments_firstname', location.hostname, '/');
    clearCookie('comments_lastname', location.hostname, '/');
    clearCookie('comments_email', location.hostname, '/');
    clearCookie('comments_homepage', location.hostname, '/');
    clearCookie('tx_comments_pi1_title', location.hostname, '/');
    clearCookie('tx_comments_pi1_firstname', location.hostname, '/');
    clearCookie('tx_comments_pi1_lastname', location.hostname, '/');
    clearCookie('tx_comments_pi1_email', location.hostname, '/');
    clearCookie('tx_comments_pi1_homepage', location.hostname, '/');
}
// cookies management
/**
 * Les lments  ne pas afficher si les cookies sociaux sont dsactivs
 * doivent tre wrapps comme ceci
 *
 * <div class="element-with-social-cookies">
 *    <!--
 *      example...
 *    -->
 * </div>
 */
export default function cookiesManagement() {
    // affiche/cache les lments de contenu type rseaux sociaux
    if (checkSocialCookieEnabled()) {
        $('.element-with-social-cookies').each(function (index, value) {
            const content = $(this).html().replace('<!--', '').replace('-->', '');
            $(this).html(content);
        });
    }
    // gestion des cookies
    if ($('#cookie-manager').length > 0 || $('.cookies-block').length > 0) {
        // Add aria-live on container
        $('#cookie-manager').attr('aria-live', 'polite');
        // creation des localstorage avec la valeur 1 s'ils n'existent pas,
        // ou s'ils ont t autoriss par l'utilisateur
        // cookies de suivi
        if (typeof localStorage.getItem('cookiesFunctional') === 'undefined' || localStorage.getItem('cookiesFunctional') === null) {
            $('input#cookies-functional').attr('checked', 'checked');
            localStorage.setItem('cookiesFunctional', 'true');
            cookiesManagement();
        } else {
            if (localStorage.getItem('cookiesFunctional') === 'true') {
                $('input#cookies-functional').attr('checked', 'checked');
            } else {
                $('input#cookies-functional').removeAttr('checked');
                removeFunctionalCookies();
            }
        }
        $('#cookie-manager .cookie-manager__submit, .cookies-block__button.-accept').click(function () {
            $('.cookies-block').hide();
            // Save to local storage state of functional cookies
            if ($('#cookies-functional').prop('checked')) {
                localStorage.setItem('cookiesFunctional', 'true');
            } else {
                localStorage.setItem('cookiesFunctional', 'false');
                removeFunctionalCookies();
            }
            if ($('.cookies-recorded').length !== 0) {
                $('.cookies-recorded').remove();
            }
            $(this).after('<p class="cookies-recorded"><span>Configuration sauvegarde</span></p>');
            // save decision to localstorage
            localStorage.setItem('cookiesConfirmed', 1);
            $('.cookies-block').slideUp();
            $('.page').removeAttr('style');
            return false;
        });
    }
    // show cookies top block
    if (typeof localStorage.getItem('cookiesConfirmed') === 'undefined' || localStorage.getItem('cookiesConfirmed') === null || localStorage.getItem('cookiesConfirmed') === 0) {
        $('.cookies-block').show();
    }
    if (window.innerWidth < 940 && $('.cookies-block').is(':visible')) {
        $('.page').css('padding-bottom', $('.cookies-block').outerHeight());
        $('.page').addClass('cookies-top-bloc--on');
    }
    $(window).resize(() => {
        if (window.innerWidth < 940 && $('.cookies-block').is(':visible')) {
            $('.page').css('padding-bottom', $('.cookies-block').outerHeight());
        } else {
            $('.page').removeClass('cookies-top-bloc--on');
            $('.page').removeAttr('style');
        }
    });
}



