import Swiper from 'swiper/dist/js/swiper';
import * as helpers from '../../js/helpers';

export default function publicationSlider() {
    const publicationBlock = document.querySelector('.publications-block.-composite');

    if (publicationBlock === null) {
        return;
    }

    const swiper = new Swiper('.publications-block__carousel', {
        effect: 'coverflow',
        grabCursor: true,
        centeredSlides: true,
        slidesPerView: 'auto',
        loop: true,
        coverflowEffect: {
            rotate: 25,
            stretch: 100,
            depth: 250,
            modifier: 1,
            slideShadows: false,
        },
        navigation: {
            nextEl: '.publications-block__control.-next',
            prevEl: '.publications-block__control.-prev',
        },
    });

    const publicationCarousel = document.querySelector('.publications-block__carousel');
    const publicationControl = document.querySelectorAll('.publications-block__control');

    if (helpers.hasClass(publicationCarousel, 'swiper-container-horizontal')) {
        for (let i = 0; i < publicationControl.length; i++) {
            publicationControl[i].style.display = 'block';
        }
    }

    window.addEventListener('resize', () => {
        swiper.init();
    });
}



