import * as helpers from '../helpers';
import SlideToggler from './slideToggler';

/*
* TODO: finish open state
* TODO: REFACTOR
* TODO: possibility to add overlay
* */

/**
 * Class represents Dropdown component
 */
export default class Dropdown {
    /**
     * Create dropdown instance.
     * @param {(String|Node)} selector - element to initialize plugin
     * @param {Object} options - Dropdown options
     */
    constructor(selector = '.js-dropdown', options = null) {
        this.selector = selector;
        this.toggler = null;
        this.dropdown = null;
        this.slideToggler = null;
        this.closeBtn = null;
        this.focusTrapHandler = null;

        // Plugin options
        this.options = {
            wrapperClass: 'js-dropdown',
            dropdownClass: 'js-dropdown__block',
            btnClass: 'js-dropdown__toggle',
            closeBtnClass: 'js-dropdown__close',
            closeBtnHtml: '<span class="ghost">Fermer</span>',
            closeHelperClass: 'has-close-btn',
            closeBtn: false,
            opened: [],
            outsideToggle: false,
            outsideToggleId: 'js-dropdown-outside-toggle',
            closeOnOutsideClick: true,
            openTypeSlide: false,
            isWrapContent: false,
            focusTrap: false,
            overflow: false,
            onInit: () => {},
            beforeOpen: () => {},
            afterOpen: () => {},
            beforeClose: () => {},
            afterClose: () => {},
        };

        if (options instanceof Object) {
            this.options = helpers.extendDefaults(this.options, options);
        }

        this.checkBeforeInit();
    }

    checkBeforeInit() {
        if (typeof this.selector !== 'string') {
            return this.selector === null ? false : this.init();
        }

        if (typeof this.selector === 'string') {
            const els = document.querySelectorAll(this.selector);

            if (els === null) {
                return;
            }

            for (let i = 0; i < els.length; i++) {
                const el = new Dropdown(els[i], this.options);
            }
        }
    }

    init() {
        this.options.onInit();
        this.create();
        this.initEvents();
    }

    manageResponsiveState() {
        if (this.options.opened.length === 0) {
            return;
        }

        const isOpened = helpers.hasClass(this.selector, '-is-open');
        const isNeedToOpen = this.options.opened.indexOf(window.responsive) !== -1;

        if (!isOpened && isNeedToOpen) {
            this.open();
        } else if (isOpened && !isNeedToOpen) {
            this.close();
        }
    }

    create() {
        const $selector = this.selector;
        let errSelector = '';

        $selector.classList.add(this.options.wrapperClass);

        if (this.options.outsideToggle) {
            this.toggler = document.getElementById(this.options.outsideToggleId);
            errSelector = `"#${this.options.outsideToggleId}"`;
        } else {
            this.toggler = $selector.querySelector(`.${this.options.btnClass}`);
            errSelector = `".${this.options.btnClass}"`;
        }

        // Handle if toggler button not found.
        if (!this.toggler) {
            throw new Error(`Dropdown. Can't find toggle button with selector: ${errSelector} for "${this.selector.className}"`);
        }

        this.dropdown = $selector.querySelector(`.${this.options.dropdownClass}`);

        if (this.options.opened.length === 0) {
            helpers.setTabindex(this.dropdown, -1);
        } else {
            helpers.setTabindex(this.dropdown, 0);
        }

        this.slideToggler = this.options.openTypeSlide && new SlideToggler(this.dropdown, {
            isWrapContent: this.options.isWrapContent,
        });

        if (this.options.closeBtn) {
            this.closeBtn = $selector.querySelector(`.${this.options.closeBtnClass}`);

            // Check if $close button is not exists
            if (this.closeBtn === null) {
                this.closeBtn = document.createElement('button');
                this.closeBtn.setAttribute('type', 'button');
                this.closeBtn.className = this.options.closeBtnClass;
                this.closeBtn.innerHTML = this.options.closeBtnHtml;
                $selector.insertBefore(this.closeBtn, $selector.children[0]);
            }

            helpers.initAriaControls([this.toggler, this.closeBtn], this.dropdown);
        } else {
            helpers.initAriaControls(this.toggler, this.dropdown);
        }
    }

    open() {
        this.options.beforeOpen();

        if (this.options.openTypeSlide) {
            this.slideToggler.open();
        }

        if (this.options.outsideToggle) {
            helpers.addClass(this.selector, '-is-open');
        } else {
            helpers.addClass(this.toggler.parentElement, '-is-open');
        }
        if (this.options.closeBtn === true && this.closeBtn !== null) {
            helpers.setAriaControls([this.toggler, this.closeBtn], this.dropdown, 'opened');
            this.closeBtn.setAttribute('tabindex', 0);
        } else {
            helpers.setAriaControls(this.toggler, this.dropdown, 'opened');
        }

        if (this.options.overflow) {
            helpers.addClass(document.body, '-overflow-hidden');
        }

        helpers.setTabindex(this.dropdown, 0);

        if (this.options.focusTrap) {
            const focusableElements = helpers.getFocusableElements(this.selector, true);
            // When open set focus on first element
            helpers.setFirstElementFocus(focusableElements);
        }
        this.options.afterOpen();
    }

    close() {
        this.options.beforeClose();

        // When closed set focus on opener button
        if (this.options.focusTrap) {
            this.toggler.focus();
        }

        if (this.options.openTypeSlide) {
            this.slideToggler.close();
        }
        if (this.options.outsideToggle) {
            helpers.removeClass(this.selector, '-is-open');
        } else {
            helpers.removeClass(this.toggler.parentElement, '-is-open');
        }

        if (this.options.overflow) {
            helpers.removeClass(document.body, '-overflow-hidden');
        }

        helpers.setTabindex(this.dropdown, -1);

        if (this.options.closeBtn === true && this.closeBtn !== null) {
            helpers.setAriaControls([this.toggler, this.closeBtn], this.dropdown, 'closed');
            this.closeBtn.setAttribute('tabindex', -1);
        } else {
            helpers.setAriaControls(this.toggler, this.dropdown, 'closed');
        }
        this.options.afterClose();
    }

    initEvents() {
        this.toggler.addEventListener('click', Dropdown.onTogglerClick.bind(this));

        if (this.options.closeBtn === true && this.closeBtn !== null) {
            this.closeBtn.addEventListener('click', this.close.bind(this));
        }

        window.addEventListener('responsive', this.manageResponsiveState.bind(this));

        if (this.options.focusTrap) {
            this.focusTrapHandler = this.initFocusTrap.bind(this);
            window.addEventListener('responsive', this.manageFocusTrap.bind(this), false);
        }

        // Close dropdown if click outside
        if (this.options.closeOnOutsideClick) {
            document.addEventListener('click', (event) => {
                const isClickInside = this.selector.contains(event.target) || event.target === this.toggler || this.toggler.contains(event.target);
                // !this.options.outsideToggle &&
                const isOpened = helpers.hasClass(this.selector, '-is-open');
                if (!isClickInside && isOpened) {
                    this.close();
                }
            });
        }
    }

    manageFocusTrap() {
        if (typeof this.options.focusTrap !== 'boolean') {
            if (!Array.isArray(this.options.focusTrap)) {
                throw new Error('focusTrap parameter should be Boolean or Array');
            }

            this.selector.removeEventListener('keydown', this.focusTrapHandler, true);
            this.options.focusTrap.forEach((resolution) => {
                if (window.responsive === resolution) {
                    this.selector.addEventListener('keydown', this.focusTrapHandler, true);
                }
            });
        } else {
            this.selector.addEventListener('keydown', this.focusTrapHandler, true);
        }
    }

    initFocusTrap(event) {
        helpers.initFocusTrap(this.selector, event, true);

        if (event.keyCode === 27) {
            this.close();
        }
    }

    destroy() {
        helpers.removeAriaControls(this.toggler.parentElement);
        this.toggler.removeEventListener('click', Dropdown.onTogglerClick.bind(this));

        if (this.options.closeBtn === true && this.closeBtn !== null) {
            this.closeBtn.removeEventListener('click', this.close.bind(this));
        }
    }

    static onTogglerClick() {
        let flag;
        if (this.options.outsideToggle) {
            flag = helpers.hasClass(this.selector, '-is-open');
        } else {
            flag = helpers.hasClass(this.toggler.parentElement, '-is-open');
        }
        if (flag === true) {
            this.close();
        } else {
            this.open();
        }
    }
}



